import * as React from 'react';
import { graphql, StaticQuery } from 'gatsby';
import UserPageLayout from '../../../common/components/layout/UserPageLayout';
import { mapEdgesToNodes } from '../../../lib/helpers';
import { Location } from '../../location/types';

type Props = {} & React.HTMLAttributes<HTMLElement>;

const query = graphql`
  query GoogleFormQuery {
    locations: allSanityClinic {
      edges {
        node {
          id
          name
          googleForm
        }
      }
    }
  }
`;

function Iframe(props: { iframe: string }) {
  return <div dangerouslySetInnerHTML={{ __html: props.iframe ? props.iframe : '' }} />;
}
const Feedback: React.FunctionComponent<Props> = () => {
  return (
    <StaticQuery
      query={query}
      render={data => {
        const locations = mapEdgesToNodes<Location>(data.locations);
        const selectedLocation = locations[0]; //0 is the index of the clinic.
        const iframe = `<iframe src="${
          selectedLocation.googleForm
        }" width="100%" height="2700" scrolling="no" frameborder="0"  marginheight="0" marginwidth="0">Loading...</iframe>`;
        return (
          <UserPageLayout title="Feedback">
            <Iframe iframe={iframe} />
          </UserPageLayout>
        );
      }}
    />
  );
};

export default Feedback;
